.Register-header{
  background-color: rgb(255, 255, 255);
  height: 11vh;
  padding: 0 25vh;
}
.Register-Content-1{
  padding: 0 26vh;
}
.Register-Content-2{
  padding: 0 26vh;
}
.Register-Content-2 img{
    width: 2.5vh;
    height: 2.5vh;
    float: left;
    margin-top: 1.5vh;
    margin-left: 20vh;
}
.Register-Content-3Registerselect{
  margin: 2vh 26vh 0;
  background-color: #fff;
  height: 42vh;
}
.header-right{
  float: right;
}
.header-left-ziti{
  float: 'left';
  height: 11vh;
  width: 40vh;
  line-height: 11vh;
  font-size: 5vh;
  margin-left: 1vh;
  font-family: 'myFont1';
  background-image: linear-gradient(to bottom , rgb(255, 156, 14), rgb(240, 123, 13));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.Register-Content-2-col-1{
  background-color: rgb(255, 156, 14);
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  -webkit-clip-path: polygon(0 0, 98% 0, 100% 50%, 98% 100%, 0 100%);
  clip-path: polygon(0 0, 98% 0, 100% 50%, 98% 100%, 0 100%);
  font-size: 18px;
  font-weight: bold;
  color: #ffffff;
}
.Register-Content-2-col-1-ziti{
float: left;
margin-top: 1.5vh;
}.Register-Content-2-col-2-ziti{
  float: left;
  margin-top: 1.5vh;

}.Register-Content-2-col-3-ziti{
  float: left;
  margin-top: 1.5vh;
}
.Register-Content-2-col-2{
  background-color: rgb(250, 205, 144);
  margin-left: -3px;
  -webkit-clip-path: polygon(0 0, 98% 0, 100% 50%, 98% 100%, 0 100%, 2% 50%);
  clip-path: polygon(0 0, 98% 0, 100% 50%, 98% 100%, 0 100%, 2% 50%);  
  font-size: 18px;
  font-weight: bold;
  color: rgb(186, 149, 98);
}
.Register-Content-2-col-3{
  background-color: rgb(250, 205, 144);
  margin-left: -3px;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%, 2% 50%);
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%, 2% 50%);
  font-size: 18px;
  font-weight: bold;
  color: rgb(186, 149, 98);
}
.Register-Content-3Registerselect-setup1{
  float: left;
  width: 40vh;
  height: 27vh;
  padding: 7vh 6vh 0;
  border: 1px solid #dce4e6;
  border-radius: 6px;
  cursor: pointer;
  text-align: center;
  border-radius: 8px;
  margin-top: 7.5vh;
  margin-left: 23vh;
}
.Register-Content-3Registerselect-setup2{
  float: right;
  width: 40vh;
  height: 27vh;
  padding: 7vh 6vh 0;
  border: 1px solid #dce4e6;
  border-radius: 6px;
  cursor: pointer;
  text-align: center;
  border-radius: 8px;
  margin-top: 7.5vh;
  margin-right: 23vh;
}
.Register-Content-3Registerselect-setup2:hover{
  border-color: rgb(255, 156, 14);  color: rgb(255, 156, 14);
}.Register-Content-3Registerselect-setup1:hover{
  border-color: rgb(255, 156, 14);  color: rgb(255, 156, 14);
}
